.edit-profile {
  margin-top: 50px;
  padding: 30px 50px;

  background-color: $basic-white;
  border-radius: 20px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1);
}

.edit-profile__list {
  @include reset-list;

  display: grid;

  grid-column-gap: 120px;
  grid-row-gap: 30px;
  grid-template-columns: 1fr 1fr;
}

.edit-profile__item {
  display: flex;
  justify-content: space-between;
}

.edit-profile__header {
  margin-right: 30px;

  font-weight: 600;
}

.edit-profile__text {
  margin-bottom: 5px;
}

.edit-profile__link {
  color: $basic-yellow;

  &:hover {
    color: $basic-yellow;

    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}
