.cabinet-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 30px;
}

.cabinet-nav__title {
  margin-bottom: 0;

  font-weight: 500;
  font-size: 14px;
  color: $special-black;
  text-transform: uppercase;
}

.cabinet-nav__list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
}

.cabinet-nav__list-item {
  margin-right: 20px;

  &:last-child {
    margin-right: 0;
  }
}

.cabinet-nav__list-link {
  font-size: 13px;
  color: $grey;
  text-transform: uppercase;

  &:hover {
    color: $grey;

    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

@media screen and (max-width: 991px) {
  .cabinet-nav {
    flex-direction: column;
    align-items: flex-start;
  }

  .cabinet-nav__title {
    margin-bottom: 20px;
  }

  .cabinet-nav__list-item {
    margin-right: 20px;
  }
}

@media screen and (max-width: 767px) {
  .cabinet-nav__list-item {
    margin-right: 40px;
    margin-bottom: 10px;
  }
}

@media screen and (max-width: 500px) {
  .cabinet-nav__list-item {
    margin-right: 30px;
    margin-bottom: 10px;
  }
}
