.partners-number {
  margin-top: 50px;
  padding: 30px;

  background-color: $basic-white;
  border-radius: 20px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1);
}

.partners-number__header {
  margin-bottom: 0;

  font-weight: 500;
  font-size: 14px;
  color: $special-black;
  text-transform: uppercase;
}

.partners-number__list {
  @include reset-list;

  display: grid;
  margin-top: 20px;
  margin-bottom: 40px;

  grid-column-gap: 60px;
  grid-template-columns: 1fr 1fr 1fr;
}

.partners-number__list-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  padding-left: 30px;

  font-weight: 600;

  background-image: url("../img/small-star.svg");
  background-repeat: no-repeat;
  background-position: left 3px;

  & span {
    margin-left: 20px;

    font-weight: 800;
    font-size: 16px;
    font-family: "Oswald", "Arial", sans-serif;
  }
}

.copy-link {
  width: 70%;
  margin-top: 15px;
  margin-right: 20px;
  padding: 5px 15px;

  background-color: $basic-white;
  border: 1px solid $basic-grey;
  border-radius: 5px;

  &:hover {
    border-color: $basic-grey;
  }

  &:focus {
    border-color: $basic-grey;
    outline: none;
  }
}

.copy-link-btn {
  margin-top: 15px;
  padding: 5px 15px;

  color: $basic-yellow;

  background-color: $basic-white;
  border: 1px solid $basic-yellow;
  border-radius: 5px;

  &:hover {
    border-color: $basic-grey;
  }

  &:focus {
    border-color: $basic-grey;
    outline: none;
  }
}

.tooltip {
  position: relative;

  display: inline-block;
}

.tooltip .tooltiptext {
  position: absolute;
  bottom: 150%;
  left: 50%;
  z-index: 1;

  width: 140px;
  margin-left: -75px;
  padding: 5px;

  text-align: center;
  color: #ffffff;

  background-color: #555555;
  border-radius: 6px;
  visibility: hidden;
  opacity: 0;

  transition: opacity 0.3s;
}

.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;

  margin-left: -5px;

  border-width: 5px;
  border-style: solid;
  border-color: #555555 transparent transparent transparent;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

@media screen and (max-width: 1100px) {
  .partners-number__list {
    grid-column-gap: 40px;
  }
}

@media screen and (max-width: 991px) {
  .partners-number {
    margin-top: 0;
  }
}

@media screen and (max-width: 767px) {
  .copy-link {
    width: 100%;
  }

  .partners-number__list {
    grid-column-gap: 70px;
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 500px) {
  .partners-number__list {
    grid-template-columns: 1fr;
  }
}
