.personal-info {
  display: grid;

  grid-gap: 30px;
  grid-template-columns: 350px 1fr;
}

.personal-info--edit {
  margin-bottom: 80px;
}

.personal-info__wrapper {
  margin-top: 50px;
  padding: 30px;

  background-color: $basic-white;
  border-radius: 20px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1);
}

.chat__list-item-img {
  width: auto;
}

.personal-info-photo__wrapper {
  display: flex;
  align-items: center;
}

.personal-info__photo-text {
  font-size: 14px;
  text-transform: uppercase;
}

.personal-info-photo {
  &:hover .personal-info__photo-input--cabinet {
    opacity: 0.7;
  }
}

.personal-info__form {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.personal-info__photo-input--cabinet {
  margin-top: -100px;

  opacity: 0;
}

.personal-info__img {
  margin-right: 20px;
  margin-bottom: 50px;

  &:hover {
    opacity: 0;
  }
}

.personal-info__photo-input {
  position: relative;

  width: 101px;
  height: 101px;
  padding: 28px 22px;
  overflow: hidden;

  font-size: 15px;
  text-align: center;
  color: $special-black;

  background-color: $basic-grey;
  webkit-border-radius: 100%;
  moz-border-radius: 100%;
  border-radius: 100%;

  ms-border-radius: 100%;
  o-border-radius: 100%;
}

.edit-profile-link {
  display: block;
  margin-top: 20px;
  padding: 10px;

  text-align: center;
  color: $special-black;
  text-decoration: none;

  background-color: $basic-grey;
  border-radius: 5px;

  &:hover {
    color: $special-black;

    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

input[type="file"] {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;

  width: 100%;

  cursor: pointer;
  opacity: 0;

  -webkit-tap-highlight-color: transparent;
}

.personal-info__photo-btn {
  margin-top: 15px;
  margin-bottom: 20px;
  padding: 8px 20px;

  color: $basic-white;

  background-color: $basic-yellow;
  border: none;
  border-radius: 4px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1);

  &:hover {
    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

.personal-info__header {
  margin-left: 20px;

  font-weight: 700;
  font-size: 18px;
  font-family: "Oswald", "Arial", sans-serif;
}

.personal-info__header--edit {
  margin-left: 0;
}

.personal-info__text {
  margin-top: 10px;
}

.personal-info__balance--wrapper {
  display: grid;

  grid-template-columns: 1fr 1fr;
}

.personal-info__balance {
  margin-bottom: 8px;

  font-weight: 400;
  font-size: 16px;

  & span {
    margin-right: 5px;

    font-weight: 800;
    font-size: 14px;
    font-family: "Oswald", "Arial", sans-serif;
  }
}

.personal-info__stars {
  margin-top: 20px;
  margin-bottom: 0;
  padding-left: 35px;

  font-size: 14px;

  background-image: url("../img/star.svg");
  background-repeat: no-repeat;
  background-position: left top;

  & span {
    font-weight: 800;
    font-size: 16px;
    font-family: "Oswald", "Arial", sans-serif;
  }
}

.personal-info__partners {
  margin-top: 20px;
  margin-bottom: 0;
  padding-left: 35px;

  font-size: 14px;

  background-image: url("../img/blue-star.svg");
  background-repeat: no-repeat;
  background-position: left top;

  & span {
    font-weight: 800;
    font-size: 16px;
    font-family: "Oswald", "Arial", sans-serif;
  }
}


.personal-info__status {
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 991px) {
  .personal-info {
    grid-template-columns: 1fr;
  }

  .personal-info__status {
    justify-content: flex-start;
  }

  .personal-info__partners {
    margin-left: 40px;
  }
}
