.personal-info-news {
  display: grid;
  margin-top: 40px;
  margin-bottom: 80px;

  grid-gap: 30px;
  grid-template-columns: 350px 1fr;
}

.chat {
  position: relative;

  height: 450px;
  padding: 30px;
  padding-right: 40px;
  padding-bottom: 170px;
  padding-left: 20px;
  overflow: auto;

  background-color: $basic-white;
  border-radius: 20px;
}

::-webkit-scrollbar {
  width: 5px;
  height: 3px;
}

::-webkit-scrollbar-button {
  display: none;

  background-color: $basic-grey;
}

::-webkit-scrollbar-track {
  background-color: #999999;
}

::-webkit-scrollbar-track-piece {
  background-color: $basic-grey;
}

::-webkit-scrollbar-thumb {
  height: 50px;

  background-color: $special-grey;
  border-radius: 5px;
}

::-webkit-scrollbar-corner {
  background-color: #999999;
}

::-webkit-resizer {
  background-color: #666666;
}

.chat__list-item-img {
  margin-top: 20px;
}

.chat__title {
  margin-bottom: 20px;

  font-size: 14px;
  color: $special-black;
  text-transform: uppercase;
}

.chat__list {
  @include reset-list;
}

.chat__list-item {
  display: grid;
  margin-bottom: 20px;

  grid-column-gap: 10px;
  grid-template-columns: 60px 1fr;

  &:last-child {
    margin-bottom: 0;
  }

  & textarea {
    width: 350px;
    min-height: 100px;
    padding-left: 10px;

    background-color: $basic-white;
    border: 1px solid $basic-grey;
    border-radius: 10px;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1);

    resize: none;

    &:focus {
      outline: none;
      opacity: 0.8;
    }
  }

  & button {
    padding: 8px 20px;

    color: $basic-white;

    background-color: $basic-yellow;
    border: none;
    border-radius: 20px;

    &:hover {
      opacity: 0.8;
    }

    &:focus {
      outline: none;
      opacity: 0.8;
    }
  }
}

.chat__list-item-header {
  order: 2;
  width: 100px;
  margin-top: 0;

  font-weight: 600;
  font-size: 16px;
}

.chat__list-item-wrapper {
  display: flex;
  flex-direction: column;
}

.chat__list-item-date {
  order: 1;
  align-self: flex-end;
  margin-bottom: 0;

  font-size: 12px;
  color: $special-black;

  opacity: 0.8;
}

.chat__list-item-text {
  margin-right: 0;
  margin-left: 0;

  font-size: 14px;
}

@media screen and (max-width: 991px) {
  .personal-info-news {
    grid-template-columns: 1fr;
  }

  .chat {
    padding: 40px;
  }

  .chat__list-item {
    justify-content: flex-start;

    & textarea {
      width: 800px;
      padding: 20px;
    }
  }

  .chat__list-item-wrapper,
  .chat__list-item-text {
    margin-left: 30px;
  }
}

@media screen and (max-width: 767px) {
  .chat__list-item {
    & textarea {
      width: 700px;
      padding: 20px;
    }
  }
}

@media screen and (max-width: 500px) {
  .chat__list-item-wrapper,
  .chat__list-item-text {
    margin-left: 20px;
  }

  .chat {
    padding: 20px;
    padding-right: 40px;
  }

  .chat__list-item {
    & textarea {
      width: 470px;
      padding: 20px;
    }
  }
}

@media screen and (max-width: 350px) {
  .chat__list-item-wrapper,
  .chat__list-item-text {
    margin-left: 10px;
  }

  .chat__list-item-header {
    font-size: 14px;
  }

  .chat__list-item-date {
    font-size: 12px;
  }

  .chat__list-item {
    & textarea {
      width: 340px;
      padding: 20px;
    }
  }
}
